<template>
  <v-app>
    <v-navigation-drawer app v-model="drawer">
      <div style="padding: 10px;">
        <v-select v-model="project_id" :label="$store.state.project.selected.name" solo dense :items="$store.state.project.projects" item-text="name" item-value="id" hide-details></v-select>
        <v-btn to="/projects/save" block small style="margin-top: 10px;"><v-icon>mdi-plus</v-icon> New Project</v-btn>
      </div>
      <v-list nav dense>
        <v-list-item-group>
          <v-list-item to="/rest-apis">
            <v-list-item-icon>
              <v-icon>mdi-api</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Rest APIs</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/functions">
            <v-list-item-icon>
              <v-icon>mdi-function</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Functions</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/environments">
            <v-list-item-icon>
              <v-icon>mdi-table-settings</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Environments</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-item to="/deployments">
            <v-list-item-icon>
              <v-icon>mdi-server</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>Deployments</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app dense flat>
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
      <v-btn icon @click="logout"><v-icon>mdi-export</v-icon></v-btn>
    </v-app-bar>
    <v-main>
      <router-view></router-view>
    </v-main>
    <v-overlay :value="overlay">
      <slot name="overlay">
        {{overlayMessage}}
      </slot>
    </v-overlay>
  </v-app>
</template>

<script>

export default {
  data() {
    return {
      drawer: false,
      overlay: false,
      overlayMessage: null,
      project_id: null
    }
  },
  watch: {
    project_id() {
      this.$store.commit('selectProject', this.project_id);
    }
  },
  async mounted() {
    this.showPageLoading('Projects are loading. Please wait...');
    await this.loadProjects();
  },
  methods: {
    async loadProjects() {
      try {
        await this.$store.commit('loadProjects');
      } catch (e) {
        if (e.response.status === 401) {
          this.logout();
        }
      } finally {
        this.hidePageLoading();
      }
    },
    logout() {
      this.$store.commit('logout');
      this.$store.commit('clearSelectedProject');
      this.$router.push({path: '/login'});
    },
    showPageLoading(message) {
      this.drawer = false;
      this.overlay = true;
      this.overlayMessage = message;
    },
    hidePageLoading() {
      this.drawer = true;
      this.overlay = false;
      this.overlayMessage = null;
    }
  }
}
</script>