import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import store from '../store';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Dashboard,
    meta: {requiresAuth: true},
    children: [
      {
        path: '/rest-apis',
        component: () => import(/* webpackChunkName: "rest_apis" */ '../views/dashboard/RestApis.vue'),
        meta: {requiresAuth: true},
      },
      {
        path: '/rest-apis/save',
        component: () => import(/* webpackChunkName: "rest_apis_save" */ '../views/dashboard/SaveRestApi.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/rest-apis/:rest_api_id/methods',
        name: 'rest-api-methods',
        component: () => import(/* webpackChunkName: "rest_api_methods" */ '../views/dashboard/RestApiMethods.vue'),
        meta: {requiresAuth: true},
      },
      {
        path: '/rest-apis/:rest_api_id/methods/save',
        name: 'rest-api-methods-save',
        component: () => import(/* webpackChunkName: "rest_api_methods_save" */ '../views/dashboard/SaveRestApiMethod.vue'),
        meta: {requiresAuth: true},
      },
      {
        path: '/functions',
        component: () => import(/* webpackChunkName: "functions" */ '../views/dashboard/Functions.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/functions/save',
        component: () => import(/* webpackChunkName: "functions_save" */ '../views/dashboard/SaveFunction.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/environments',
        component: () => import(/* webpackChunkName: "environments" */ '../views/dashboard/Environments.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/environments/save',
        component: () => import(/* webpackChunkName: "environments_save" */ '../views/dashboard/SaveEnvironment.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/deployments',
        component: () => import(/* webpackChunkName: "deployments" */ '../views/dashboard/Deployments.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/projects',
        component: () => import(/* webpackChunkName: "projects" */ '../views/dashboard/Projects.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/projects/save',
        component: () => import(/* webpackChunkName: "projects_save" */ '../views/dashboard/SaveProject.vue'),
        meta: {requiresAuth: true}
      },
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import(/* webpackChunkName: "register" */ '../views/Register.vue')
  }
]

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  const requiresAuth = to.matched.some(x => x.meta.requiresAuth)

  if (requiresAuth && !store.state.account.token) {
    next('/login')
  } else {
    next();
  }
})

export default router
